:root {
  --color-base-primary: #01b075;

  --color-base-accent-0: #c3e1e1;
  --color-base-accent-1: #a3e2cd;
  --color-base-accent-2: #52c9a1;
  --color-base-accent-3: #0d4a35;

  --color-base-neutral-0: #fff;
  --color-base-neutral-1: #fdfbfe;
  --color-base-neutral-2: #efebf1;
  --color-base-neutral-3: #707c8c;
  --color-base-neutral-4: #000;

  --color-base-invalid-primary: #ff4838;
  --color-base-invalid-secondary: #ffd9d6;
  --color-base-invalid-accent: #fe6456;
  --color-base-transparent: rgba(0,0,0,0);

  --border-hidden: 1px solid var(--color-base-transparent);
  --border-small-neutral: 1px solid var(--color-base-neutral-2);
  --border-accent: 1px solid var(--color-base-neutral-3);

  --border-radius-soft: 8px;
  --transistion-fast: all 0.3s ease;

  --padding-button: 0.8rem 0.8rem;

  --margin-left-medium: 0 0 0 1.25rem;
  --margin-base: 0.3rem 1rem 0.3rem 0;

  --font-family: 'Roboto', Arial;

}

.App {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
}

.notification__wrapper {
  position: absolute;
  top: -20rem;
  display: flex;
  justify-content: center;
  width: 100%;
  z-index: 10;
  transition: all 0.3s ease;
}

.notification__item {
  position: relative;
  padding: 1rem;
  width: 0;
  background-color: var(--color-base-invalid-secondary);
  border-radius: var(--border-radius-soft);
  color: var(--color-base-invalid-primary);
  font-weight: 700;
  overflow: hidden;
  
}

.notification__open > .notification__item {
  width: auto;
}

.notification__open {
  top: 1rem;
}

@media (max-width: 640px) {
  .App {
    flex-direction: column;
    height: unset;
  }

  .menu__container {
    width: 100%;
    border-right: var(--border-hidden);
    border-bottom: var(--border-small-neutral);
  }

  .dashbord__container {
    padding: 0;
    margin: 0;
  }

  .dashboard__itemcontainer {
    margin: 0;
  }

  .dashbord__row {
    flex-direction: column;
  }

  .container__large {
    display: none;
  }

  .container__medium {
    margin-top: 0.5rem;
    width: 100%;
  }

  .container__small {
    margin-top: 0.5rem;
    width: 100%;
  }

  .user__container {
    margin: 0;
    flex-direction: column;
  }

  .usertable__container {
    margin: 0.5rem 0;
  }

  .column-small {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 2rem;
  }

  .column-small, .column-medium, .column-large {
    width: 100%;
    text-align: center;
  }

  .user__column:last-child {
    display: flex;
    justify-content: center;
    margin-top: 1rem;
  }
}

@media (min-width: 641px) and (max-width: 1280px) {
  .container__medium {
    margin-top: 0.5rem;
    width: 100%;
  }

  .container__small {
    margin-top: 0.5rem;
    width: 100%;
  }

  .dashbord__container {
    padding: 0;
  }

  .user__column.tablet-hide {
    display: none;
  }
}

