.ticker__container {
    display: flex;
}

.ticker__item {
    padding: 0.5rem 3rem;
    width: 25%;
    border-right: var(--border-small-neutral);
}

.ticker__item:first-child {
    padding-left: 1rem;
}

.ticker__item:last-child {
    padding-right: 1rem;
    border: none;
}

.ticker__header {
    margin: 0;
    color: var(--color-base-neutral-3);
    font-weight: 400;
    font-size: 1rem;
}

.ticker__data {
    margin: 0.3rem 0;
    font-size: 2rem;
    font-weight: 700;
}

.ticker__change-up {
    display: flex;
    align-items: center;
    margin: 0;
    color: var(--color-base-accent-2);

}

.ticker__change-down {
    display: flex;
    align-items: center;
    margin: 0;
    color: var(--color-base-invalid-primary); 
}

.ticker__icon-up {
    margin-right: 0.5rem;
    fill: var(--color-base-accent-2);
    transform: rotate(45deg);
}

.ticker__icon-down {
    margin-right: 0.5rem;
    fill: var(--color-base-invalid-accent);
    transform: rotate(135deg);
}