.user__container {
    position: relative;
    display: flex; 
    flex: 1;
    padding: 0 1em;
    flex-direction: row;
    align-items: center;
    box-sizing: border-box;
}

.user__column {
    padding: 0.5rem 1rem;
}

.column-small {
    width: 4%;
}

.column-medium {
    width: 10%;
}

.column-large {
    width: 15%;
}


.user__profile {
    position: relative;
    height: 3rem;
    width: 3rem;
    border-radius: 50%;
    overflow: hidden;
}

.user__picture {
    height: 100%;
    width: 100%;
    object-fit: cover;
}

.user__name {
    font-size: 1.15rem;
    font-weight: 700;
}

.user__attribute {
    font-size: 1rem;
    font-weight: 400;
}

.user__status {
    display: flex;
    justify-content: center;
    padding: 0.3rem 0.5rem;
    width: 3rem;
    height: 1rem;
    font-size: 0.8rem;
    color: var(--color-base-accent-3);
    background-color: var(--color-base-accent-0);
    border-radius: 2rem;
}

.user__status-inactive {
    display: flex;
    justify-content: center;
    padding: 0.3rem 0.8rem;
    width: 3rem;
    height: 1rem;
    font-size: 0.8rem;
    color: var(--color-base-invalid-primary);
    background-color: var(--color-base-invalid-secondary);
    border-radius: 2rem;
}