.usertable__container {
    display: flex;
    flex-direction: column;
    margin: 1rem;
    padding: 1rem 0;
    min-width: 5rem;
    width: 100%;
    min-height: 5rem;
    background-color: var(--color-base-neutral-0);
    border-radius: var(--border-radius-soft);
    border: var(--border-small-neutral);
    box-sizing: border-box;
}

.usertable__header {
    display: flex;
    margin: 0 1rem;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
}

.usertable__headertitle {
    margin: 0 1em;
    padding: 0;
    font-size: 1.15rem;
    font-weight: 700;
    color: var(--color-base-neutral-4);
}

.usertable__actions {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
}

.usertable__button {
    position: relative;
    display: flex;
    align-items: center;
    margin: 0 1em;
    padding: 0.5rem 0.8rem;
    align-items: cen ter;
    border-radius: 5px;
    font-size: 1.05rem;
    font-weight: 700;
    background-color: #f3f3f4;
    text-decoration: none;
    transition: 0.3s ease;
    border: none;
    outline: none;
    border: 1px solid rgba(0,0,0,0);
    cursor: pointer;
}

.usertable__button:hover {
    background-color: var(--color-base-accent-0);
}

.usertable__button:focus, .usertable__button:active {
    background-color: var(--color-base-accent-1);
}

.usertable_filterbutton {
    position: relative;
    display: flex;
    align-items: center;
    margin: 0 1em;
    padding: 0 0 0 0.8rem;
    align-items: center;
    font-size: 1.05rem;
    font-weight: 700;
    background-color: #fff;
    border: none;
    outline: none;
    cursor: pointer;
    
}

.usertable_filterbutton svg {
    transition: 0.2s ease;
}

.filter-open {
    transform: rotate(-180deg);
}

.usertable__filters {
    position: relative;
    display: flex;
    flex-direction: row;
    align-items: center;
    flex-wrap: wrap;
    margin: 1em 2em;
    height: 0;
    transition: height 300ms cubic-bezier(0.4, 0, 0.2, 1);
    overflow: hidden;
}

.usertable__switch {
    display: flex;
    margin: var(--margin-base);
}

.filterbar-open {
    height: auto;
}

.usertable__icon {
    margin: 0 0.3rem 0 0;
}

.usertable__selectfilter {
    margin: var(--margin-base);
    padding: 0.5em;
    border-radius: 0.5em;
    border: none;
    border: 1px solid rgba(0,0,0,0);
    background-color: #f3f3f4;
    transition: 0.5s ease;
    cursor: pointer;
}

.usertable__selectfilter:hover {
    box-shadow: 0 0 0 2px var(--color-base-accent-0);
    outline: none;
    background-color: var(--color-base-neutral-0);
}

.usertable__selectfilter:focus, .usertable__selectfilter:active {
    border: 1px solid var(--color-base-accent-2);
    box-shadow: 0 0 0 2px var(--color-base-accent-0);
    background-color: var(--color-base-neutral-0);
    outline: none;
}

.usertable__selectfilter:first-of-type {
    border-radius: 0.5em;
}

.usertable__search {
    position: relative;
    display: flex;
    align-items: center;
    width: 15em;
    height: 2em;
    margin: var(--margin-base);
    padding: 0 1em;
    background-color: #f3f3f4;
    border-radius: 0.5em;
    border: none;
    border: 1px solid rgba(0,0,0,0);
    transition: 0.5s ease;
}

.usertable__searchinput {
    height: 100%;
    margin: 0;
    padding: 0;
    flex: 1;
    background-color: transparent;
    outline: none;
    border: none;
}

.usertable__searchicon {
    width: 1rem;
}
  
.usertable__search:hover {
    box-shadow: 0 0 0 2px var(--color-base-accent-0);
    outline: none;
    background-color: #fff;
}
  
.usertable__search:focus, .usertable__search:active, .usertable__search:focus-within {
    border: 1px solid var(--color-base-accent-2);
    box-shadow: 0 0 0 2px var(--color-base-accent-0);
    background-color: #fff;
    outline: none;
}

.usertable__searchbutton {
    margin: 0;
}


.usertable__buttongroupitem {
    display: inline-block;
    width: 4rem;
    padding: 0.3rem 1rem; 
    border-color: #f3f3f4;
    border-style: solid;
    border-width: 3px 1px 3px 1px;
    border-radius: 0;
    cursor: pointer;
    text-align: center;
}

.usertable__buttongroupitem:first-of-type {
    border-top-left-radius: 5px;
    border-bottom-left-radius: 5px;
    border-width: 3px 1px 3px 3px;
}

.usertable__buttongroupitem:last-of-type {
    border-top-right-radius: 5px;
    border-bottom-right-radius: 5px;
    border-width: 3px 3px 3px 1px;
}


.usertable__buttongroupinput {
    display: none;
}

.usertable__buttongroupinput:checked + .usertable__buttongroupitem {
    background-color: #f3f3f4;
    font-weight: 600;
}