.dashbord__container {
    display: flex;
    flex-direction: column;
    padding: 2rem;
    width: 100%;
    height: 100%;
    background-color: #fbf9fc;
    box-sizing: border-box;
}

.dashbord__row {
    position: relative;
    display: flex;
    flex-direction: row;
}

.dashboard__itemcontainer {
    position: relative;
    display: flex;
    flex-direction: column;
    margin: 1rem;
    padding: 1.5rem 2rem 2rem 2rem;
    background-color: var(--color-base-neutral-0);
    border-radius: var(--border-radius-soft);
    border: var(--border-small-neutral);
    box-sizing: border-box;
}

.container__large {
    flex: 1;
    min-height: 5rem;
    padding: 1rem;
}

.container__medium {
    width: 60%;
    height: 25rem;
}

.container__small {
    width: 40%;
    height: 25rem;
}

.dashboard__title {
    margin: 0;
    padding: 0;
    font-size: 1.15rem;
    font-weight: 700;
    color: var(--color-base-neutral-4);
}