.menu__container {
    margin: 0;
    padding: 1rem;
    width: 23rem;
    background-color: var(--color-base-neutral-0);
    border-right: var(--border-small-neutral);
    box-sizing: border-box;
}

.menu__logo {
    text-align: center;
    font-size: 3rem;
    font-weight: 900;
}

.menu__link{
    position: relative;
    display: flex;
    align-items: center;
    width: 100%;
    margin: 0.5rem 0;
    padding: var(--padding-button);
    border-radius: var(--border-radius-soft);
    background-color: var(--color-base-neutral-0)e;
    font-size: 1.05rem;
    font-weight: 700;
    color: var(--color-base-neutral-4);
    text-decoration: none;
    transition: var(--transistion-fast);
    border: none;
    outline: none;
    cursor: pointer;
}

.menu__link:focus, .menu__link:hover {
    background-color: var(--color-base-accent-0);
}

.menu__link:focus > *, .menu__link:hover > * {
    color: var(--color-base-accent-3);
}

.menu__link:active {
    background-color: var(--color-base-accent-1);
}

.menu__link-active, .menu__link-active {
    background-color: var(--color-base-primary);
}

.menu__link-active > *, .menu__link-active > * {
    color: var(--color-base-neutral-0);
}

.menu__link-active:focus, .menu__link-active:hover {
    background-color: var(--color-base-primary);
}

.menu__link-active:focus > *, .menu__link-active:hover > * {
    color: var(--color-base-neutral-0);
}


.menu__icon {
    margin-right: 1.10rem;
}