.userchart__subheader {
    display: flex;
    align-items: center;
    margin: 0.5rem 0;
}

.userchart__text-highlight {
    color: var(--color-base-accent-2); 
    margin: 0 0.3rem 0 0.3rem;
    font-weight: 700;
}

.userchart__icon {
    margin-right: 0.25rem;
    fill: var(--color-base-accent-2);
    transform: rotate(45deg);
}

.userchart__chartcontainer {
    display: flex;
    position: relative;
    flex: 1;
    align-items: center;
}