.form__content {
    position: relative;
    display: flex;
    flex-direction: column;
    flex: 1;
    justify-content: space-between;
    align-items: flex-start;
    text-align: left;
    width: 100%;
    height: 100%;
    padding: 1rem 2rem;
    box-sizing: border-box;
}

.form__subtitle {
    margin: 0.5rem 0;
}

.form__subtitle:first-child {
    margin: 0 0 0.5rem 0;
}

.form__container {
    width: 100%;
    height: 100%;
}

.form__attribute {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin: 1rem 0 0 0;
}

.form__attribute:first-child {
    margin: 0 0 0 0;
}

.form__nav {
    position: relative;
    width: 100%;
    height: 3rem;
}

.form__navbutton {
    position: relative;
    display: flex;
    margin: 0 1em 0 0;
    padding: 0.5rem 0.8rem;
    border-radius: 5px;
    font-size: 1.05rem;
    font-weight: 700;
    background-color: #f3f3f4;
    text-decoration: none;
    border: none;
    outline: none;
    border: 1px solid rgba(0,0,0,0);
    cursor: pointer;
}

.form__navbutton:last-child {
    position: absolute;
    right: 0;
    top: 0;
    margin-right: 0;
}

.form__navbutton:hover {
    background-color: var(--color-base-accent-0);
}

.form__navbutton:focus, .form__navbutton:active {
    background-color: var(--color-base-accent-1);
}

.form__inp {
    position: relative;
    display: flex;
    align-items: center;
    width: 100%;
    margin: 0.5em 0 0.5em 0;
    padding: 0.5em 1em;
    background-color: #f3f3f4;
    font-family: var(--font-family);
    border-radius: 0.5em;
    border: none;
    outline: none;
    border: 1px solid rgba(0,0,0,0);
    transition: 0.5s ease;
    box-sizing: border-box;
}

.form__inp:hover {
    box-shadow: 0 0 0 2px var(--color-base-accent-0);
    outline: none;
    background-color: #fff;
}
  
.form__inp:focus, .form__inp:active, .form__inp:focus-within {
    border: 1px solid var(--color-base-accent-2);
    box-shadow: 0 0 0 2px var(--color-base-accent-0);
    background-color: #fff;
    outline: none;
}

.form__inpselect {
    margin: 0 1rem;
    padding: 0.5em;
    width: 100%;
    font-family: var(--font-family);
    text-align: left;
    border-radius: 0.5em;
    border: 1px solid rgba(0,0,0,0);
    background-color: #f3f3f4;
    transition: 0.5s ease;
    cursor: pointer;
}

.form__inpselect:first-of-type {
    margin: 0 1rem 0 0;
}

.form__inpselect:hover {
    box-shadow: 0 0 0 2px var(--color-base-accent-0);
    outline: none;
    background-color: var(--color-base-neutral-0);
}

.form__inpselect:focus, .form__inpselect:active {
    border: 1px solid var(--color-base-accent-2);
    box-shadow: 0 0 0 2px var(--color-base-accent-0);
    background-color: var(--color-base-neutral-0);
    outline: none;
}

.form__label {
    font-size: 0.9rem;
}

.form__validationmessage {
    display: none;
    font-size: 0.85rem;
    color: var(--color-base-invalid-primary);
}

.form__validationmessage-active {
    display: inline;
}

.form__blockchoice {
    position: relative;
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 1rem;
    margin-bottom: 1rem;
}

.form__blockchoice-label {
    position: relative;
    padding: 0.5rem 1rem;
    text-align: center;
    border-color: #f3f3f4;
    border-style: solid;
    border-width: 3px;
    border-radius: 5px;
    box-sizing: border-box;
    cursor: pointer;
}

.form__blockchoice-inp {
    display: none;
}

.form__blockchoice-inp:checked + .form__blockchoice-label {
    border: 3px solid var(--color-base-accent-2);
    background-color: var(--color-base-accent-0);
}

.form__blockchoice-icon {
    height: 1.25rem;
    width: 100%;
    margin: 0.3rem;
}