.modal__container {
    position: absolute;
    display: none;
    left: 0;
    right: 0;
    top: 20rem;
    margin: auto;
    width: 25rem;
    height: 27rem;
    background: #fff;
    box-shadow: 0 5px 10px 2px rgba(0, 0, 0, 0.05);
    border-radius: 5px;
    text-align: center;
    z-index: 30;
}

.modal__overlay {
    position: absolute;
    display: none;
    height: 100%;
    width: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    z-index: 29;
}
  
.modal-open {
    display: block;
}



.modal__header {
    position: relative;
    display: flex;
    flex-direction: row;
    justify-content: center;
    width: 100%;
    padding: 1rem;
    box-sizing: border-box;
}

.modal__title {
    margin: 0;
}


.modal__close {
    position: absolute;
    top: 0;
    right: 0;
    padding: 1rem;
    display: flex;
    align-items: center;
    cursor: pointer;
    border: none;
    font-size: 20px;
    background: none;
}

.modal__wrapper {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100%;
}